import type { Controller } from '@ali/4ever-cangjie';

export default function canToggleMark(editor: Controller) {
  const { value } = editor;
  const { isCollapsedAtEdge } = value.selection;
  if (isCollapsedAtEdge) {
    return false;
  }
  return !editor.query('isSelectionInImage') && !editor.query('isSelectionInHr') && !editor.query('isSelectionInLinkCard');
}
